.footer{
    color: $white;

}

.footer-area{
    // max-width: 1200px;
    margin: 0 auto;
    padding: 36px 0 28px 0;
}

.footer-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
}

.f-top-left,.f-bottom-right{
    li{
        font-size: 1rem;
        line-height: 1.5;
    }
    a{
        text-decoration: none;
        color: $white;
    }
}

.f-top-right,.footer-bottom,.f-bottom-right,.f-top-left,.f-bottom-left{
    display: flex;
}

.f-top-right{
    li{
        margin-left: 32px;
    }
}

.footer-p{
    display: flex;
    align-items: center;
    font-size: 2.25rem;
    line-height: 1.5;
    margin-bottom: 16px;
}

.f-m-list{
    margin-left: 48px;
}

.footer-bottom{
    justify-content: space-between;
}


.f-bottom-left,.f-bottom-right{
    align-items: center;
}

.f-bottom-left{
    li{
        margin-right: 38px;
    }
}

.f-m-list{
    margin-left: 48px;
}

.f-second-p{
    border-bottom: 1px solid #fff;
    padding-bottom: 24px;
}

.f-img1{
    margin-right: 23px;
}

.f-img2{
    margin-right: 12px;
}

.f-bottom-right{
    li{
        margin-left: 62px;
    }
}