.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 48px;
}

.menu{
    display: none;
}

.logo{
    width: 80px;
    display: block;
}

.h-list-area{
    display: flex;
    margin: 40px 0;

}

.h-list-box{
    margin-left: 64px;
    border-bottom: 2px solid $white;
    border-color: rgba(0, 0, 0, 0);
    padding-bottom: 6px;
    transition: .3s;

    &:hover{
        border-bottom: 2px solid $white;
    }
    a{
        text-decoration: none;
        color: $white;
        font-size: 1.25rem;
        line-height: 1.4;
        display: block;
    }
}