.f-banner-area,.f-content-area{
    margin: 0 auto;
}

.f-banner{
    background-image: url(../images/faq/72.png);
    h2{
        display: flex;
        padding-top: 214px;
        p{
            font-size: $l-text;
        }
    }
}

.f-content-area{
    padding: 88px 0 156px 0;
    h3{
        font-size: 2rem;
    }
}

.f-c-list{
    border: 1px solid $black;
    margin-top: 16px;
}


.l-box,.li-area{
    border-bottom: 1px solid $black;
}

.d-f{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.d-f,.q1-box{
    padding: 22px 38px;
}

.fsz-20{
    font-size: 1.25rem;
    font-weight: bold;
}

.q1-box{
    line-height: 1.5;
    background-color: #f2f2f2;
}
