.checkbox-area{
    position: relative;
    cursor: pointer;
    margin-right: 0.4rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
    }
}

.checkbox-p{
    margin-left: 34px;
    display: block;
}

.checkmark{
    position: absolute;
    top: 0;
    left: 0;
    height: 24px;
    width: 24px;
    border: 1px solid #000;
    border-radius: 3px;
    background-color: transparent;
}

.checkbox-area:hover input ~ .checkmark {
    background-color: #ccc;
}

.checkbox-area input:checked ~ .checkmark {
    background-color: $main-red;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.checkbox-area input:checked ~ .checkmark:after {
    display: block;
}

.checkbox-area .checkmark:after {
    left: 7px;
    top: 3px;
    width: 5px;
    height: 10px;
    border: solid $white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.confirm-area{
    padding-top: 40px;
    display: flex;
    align-items: center;
